<template>
  <div style="display: inline-block;">
    <div
      v-for="index in maxValue"
      :key="index"
      class="iconfont icon-star star-item star-placeholder"
    >
    <!-- <div
      v-for="index in maxValue"
      :key="index"
      @mousemove="mousemoveStarItem($event, index)"
      class="iconfont icon-star star-item star-placeholder"
    > -->
      <div
        class="iconfont icon-star star-item star-active"
        :style="{ width: getWidth(index), color: activeColor }"
        style="background-image: linear-gradient(to right, #ff2e7c, #ed1c24);-webkit-background-clip: text;-webkit-text-fill-color: transparent;"
      />
    </div>
<!--    <span class="star-value" :style="{  color: activeColor }">-->
<!--      {{ starValue }}-->
<!--    </span>-->
  </div>
</template>

<script>
export default {
  data() {
    return {
      // value: 1.5,
    };
  },
  props: {
    value:{
      type: Number,
      default: 2.5,
    },
    maxValue: {
      type: Number,
      default: 5,
    },
    activeColor: {
      type: String,
      default: "#ff7e28",
    },
  },
  computed: {
    starValue() {
      return this.value.toFixed(1);
    },
  },
  methods: {
    getWidth(index) {
      const gap = this.value - index + 1;
      if (gap >= 1) {
        return "100%";
      } else if (gap <= 0) {
        return 0;
      } else {
        return gap * 100 + "%";
      }
    },
    mousemoveStarItem(e, index) {
      const width = e.currentTarget.offsetWidth;
      this.value = index + e.offsetX / width - 1;
    },
  },
};
</script>


<style scoped>
@font-face {
  font-family: "iconfont";
  src: url("data:application/x-font-woff2;charset=utf-8;base64,")
    format("woff2");
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 0.5vw;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-star:before {
  content: "\e627";
}
.star-item {
  display: inline-block;
  font-size: 1vw;
}

.star-placeholder {
  cursor: pointer;
  position: relative;
  color: #666666;
}
.star-active {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.star-value {
  font-size: 1vw;
  margin-left: 10px;
}
</style>