<template>
<div class="subtitle">
  {{text}}
  <span style="display: inline-block;float:right;color: #ffffffe6;padding-right: 5%;">{{ text2 }}</span>
</div>
</template>

<script>
export default {
    props:['text','text2'],
}
</script>

<style>
.subtitle {
  position: relative; /* 让 ::after 伪元素相对于 h2 元素定位 */
  display: inline-block; /* 让 ::after 伪元素的宽度与文字宽度一致 */
  padding-bottom: 5px; /* 给文字下方留出一定空间，用于显示直线 */
  width: 100%;
  padding-left: 3%;
  text-align: left;
  font-family: "苹方-简体";
  font-size: 10px;
  color: #ffffffe7;
}

.subtitle::after {
  content: ""; /* 设置伪元素的内容为空 */
  position: absolute; /* 绝对定位 */
  bottom: 0; /* 线条与文字底部对齐 */
  left: 0; /* 线条左对齐 */
  width: 91%; /* 宽度设置为 100%，覆盖 h2 元素的宽度 */
  margin-left: 3%;
  border-bottom: 1px solid #cccccc64;
  
}

</style>

